import './Navig.css'


function Navig({handleClick}) {
  return (
  	<div >
       <nav className="btn-group">
          <button  onClick={() => handleClick('target')} className="buttondisp"> Target </button>
          <button  onClick={() => handleClick('macys')} className="buttondisp"> Macys </button>
          <button  onClick={() => handleClick('walmart')} className="buttondisp"> Walmart </button>
          <button  onClick={() => handleClick('jcpenny')} className="buttondisp"> JcPenny </button>
          <button  onClick={() => handleClick('costco')} className="buttondisp"> Costco </button>
          <button  onClick={() => handleClick('ashley')} className="buttondisp"> Ashley </button>
          <button  onClick={() => handleClick('lowes')} className="buttondisp"> Lowes </button>
          <button  onClick={() => handleClick('staples')} className="buttondisp"> Staples </button>
          <button  onClick={() => handleClick('homedepot')} className="buttondisp"> HomeDepot </button>
          <button  onClick={() => handleClick('bestbuy')} className="buttondisp"> BestBuy </button>
          <button  onClick={() => handleClick('amazon')} className="buttondisp"> Amazon </button>
          <button  onClick={() => handleClick('kohls')} className="buttondisp"> Kohls </button>
       </nav>
       <div className="clear"></div>
    </div>
  );
}

export default Navig;