import './App.css';
import ScanImage from './ScanImage';
import {useState,useEffect} from 'react';
import Navig from './Navig';
import {id} from './Urlinfo';

function App() {
  const [url,seturl] =useState([]);
  // const [bgColor,setbgColor] =useState('#4CAF50');
  const generateurl = (start,end) => {
    var arr = [];
    while(start < end+1){
      var l ="https://i.blackfriday.com/imagery/ad-scan-pages/"+start+".1605576978.fit_lim.size_850x.jpg"
      arr.push(l);
      start++;
      }
      return arr
  }
  const handleClick = (x) => {
    const y =id.filter(item=> item.name === x)
    seturl(generateurl(y[0].start,y[0].end))
  }
  useEffect (() =>{},[url])
  return (
    <div className="App">
        <Navig  handleClick={handleClick} />
        <ScanImage url={url} />
    </div>
  );

}

export default App;