
import './ScanImage.css'


function ScanImage({url}) {
  return (
     <div className='griddisplay'>
     {
     	url.map((item,i)=> {
     		return (<a key={i} >
     			<img className='scanimage' alt='scanimage' src={item} />
     			</a>
     			) 
     	})
     }
    </div>
  );
}

export default ScanImage;
